import React from "react";

function Data(props) {
  return (
    <div>
      <h2> EMPLOYEE FEEDBACK DATA</h2>

      <div className="div2">{props.paras}</div>

      <button type="button" onClick={props.goback} className="goback">
        Goback
      </button>
    </div>
  );
}

export default Data;
