import React from "react";
import "../styles.css";

function Form(props) {
  return (
    <div className="div">
      <h2>EMPLOYEE FEEDBACK FORM</h2>

      <form className="form">
        Name: <input type="text" name="name" onChange={props.name} />
        <br />
        Department:{" "}
        <input type="text" list="department" onChange={props.department} />
        <datalist id="department">
          <option value="Mechanical Eng" />
          <option value="Civil Eng" />
          <option value="Computer Science" />
        </datalist>
        <br />
        Rating: <input type="text" list="rating" onChange={props.rating} />
        <datalist id="rating">
          <option value="1" />
          <option value="2" />
          <option value="3" />
        </datalist>
        <br />
        <button type="button" onClick={props.submit}>
          SUBMIT
        </button>
      </form>
    </div>
  );
}
export default Form;
