import "./styles.css";
import Form from "./components/form";
import Data from "./components/data";
import React, { useState } from "react";

var arrays = [];
var key = 0;

function Paragraph(lis) {
  return (
    <p className="paragraph">
      Name: {lis.emptyname} | Department: {lis.emptydepartment} | rating:
      {lis.emptyrating}
    </p>
  );
}

function Updated() {
  return arrays.map(Paragraph);
}

function App() {
  const [t, f] = useState(true);
  const [before, after] = useState("");

  const [emptyname, fullname] = useState("");
  const [emptydepartment, fulldepartment] = useState("");
  const [emptyrating, fullrating] = useState("");

  function Name(event) {
    fullname(event.target.value);
    console.log(emptyname);
  }
  function Department(event) {
    fulldepartment(event.target.value);
  }
  function Rating(event) {
    fullrating(event.target.value);
  }

  function Submit(e) {
    arrays.push({ emptyname, emptydepartment, emptyrating, key });
    f(!t);
    key++;
    after(Updated);
  }

  function Goback() {
    f(!t);
  }

  return (
    <div className="App">
      {t && (
        <Form
          name={Name}
          department={Department}
          rating={Rating}
          submit={Submit}
        />
      )}
      {t ? null : <Data paras={before} goback={Goback} />}
    </div>
  );
}

export default App;
